<template>
  <div class="price">
        <van-nav-bar
            title="选择颜色"
            left-text="返回"
            left-arrow
            @click-left="$router.go(-1)"
        />
      <div class="wrap">
          <div class="choose_wrap">
              <div class="tag" v-if="choose.id">{{choose.name}}</div>
              <div class="clear" @click="clear">
                    <p>清除</p>
                    <van-icon name="cross" />
              </div>
          </div>
          <div class="list">
              <div>
                <div class="item"  @click="chooseBtn(item)" v-for="item in list" :key="item.id">
                    <p :class="item.id == choose.id ? 'active' : ''">{{item.name}}</p>
                </div>
              </div>
          </div>
      </div>

      <div class="btn-wrap">
          <van-button type="primary" size="large" color="#2873ff" @click="submit">确定</van-button>
      </div>
  </div>
</template>
 
<script>
import { mapState, mapGetters } from "vuex";
import api from '../api/index'
export default {
  name: 'price',
  data () {
    return {
        choose: {
            name: '',
            id: ''
        },
        list: []
    }
  },
  computed: {
      ...mapState({
          s_sex: state => state.s_sex,
          s_price: state => state.s_price
      })
  },
  created() {
      console.log( this.s_price )
      this.choose = this.s_price;
      api.tableInfo().then(res => {
        console.log(res.data.data)
        this.list = res.data.data.chairColor;
      })
  },
  methods: {
    clear() {
        this.choose = {
            name: '',
            id: ''
        }
    },
    chooseBtn(val) {
        console.log( val )
        this.choose = val;
    },
    submit() {
        this.$store.state.s_color = this.choose;
        this.$store.state.s_color.error = false;
        this.$store.state.s_color.err_msg = '';
        this.$router.go(-1);
    }
  }
}
</script>
 
<style scoped lang ="stylus">
.price
    .wrap
        .choose_wrap
            position relative
            background #EEE
            height 48px
            .tag
                background #FFF
                display inline-block
                padding 4px
                position absolute
                top 12px
                left 12px
            .clear
                display flex
                align-items center
                position absolute
                right 12px
                top 12px
                color #2873FF
                padding 4px
        .list
            padding 12px
            .item
                padding 6px 6px
                display inline-block
                width 25%
                box-sizing border-box
                p 
                    background #EEE
                    width 100%
                    text-align center
                    line-height 40px
                .active
                    background rgba(40,115,255,0.1)
                    color #2873ff
    .btn-wrap
        position fixed
        bottom 0
        left 0
        width 100%










</style>